import { AnimationEvent } from '@angular/animations';
import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  HostBinding,
  HostListener,
  Input,
  Output,
} from '@angular/core';
import { Article } from '../api/article';
import { helpExtFlyoutAnimations } from './help-ext-flyout-animations';

export type FlyoutVisibility = 'initial' | 'visible' | 'hidden';

@Component({
  selector: 'help-ext-flyout',
  templateUrl: './help-ext-flyout.template.html',
  animations: [helpExtFlyoutAnimations.flyoutState],
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    class: 'help-ext-flyout',
  },
})
export class HelpExtFlyoutComponent {
  @Input()
  article: Article;

  @Output()
  mouseEnter = new EventEmitter<void>();

  @Output()
  mouseLeave = new EventEmitter<void>();

  @Output()
  afterHidden = new EventEmitter<void>();

  @HostListener('mouseenter')
  _onMouseEnter() {
    this.mouseEnter.emit();
  }

  @HostListener('mouseleave')
  _onMouseLeave() {
    this.mouseLeave.emit();
  }

  @HostBinding('@state')
  _visibility: FlyoutVisibility = 'initial';

  @HostListener('@state.done', ['$event'])
  _onStateDone(event: AnimationEvent) {
    const toState = event.toState as FlyoutVisibility;

    if (toState === 'hidden' && !this.isVisible()) {
      this.afterHidden.emit();
    }
  }

  show(): void {
    this._visibility = 'visible';
  }

  hide(): void {
    this._visibility = 'hidden';
  }

  isVisible(): boolean {
    return this._visibility === 'visible';
  }
}
