import { HttpHeaders } from '@angular/common/http';
import { InjectionToken, Provider } from '@angular/core';
import { Observable } from 'rxjs';

export abstract class HelpExtUrlResolver {
  abstract resolve(): string | Observable<string>;
}

export type HelpExtHttpHeaders = HttpHeaders | { [header: string]: string | string[] };

export interface HelpExtConfig {
  helpExtUrl?: string;
  helpExtUrlResolver?: Provider;
  cacheLifetimeSecond?: number;
  httpHeaders?: HelpExtHttpHeaders;
}

export const HELP_EXT_URL_TOKEN = new InjectionToken<string>('HELP_EXT_URL');
export const HELP_EXT_CACHE_LIFETIME_TOKEN = new InjectionToken<string>('HELP_EXT_CACHE_LIFETIME');
export const HELP_EXT_HTTP_HEADERS = new InjectionToken<string>('HELP_EXT_HTTP_HEADERS');
