import { HttpClient } from '@angular/common/http';
import { ElementRef, Inject, Injectable, SecurityContext } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { isObservable, Observable, of, throwError } from 'rxjs';
import { catchError, finalize, map, mergeMap, share, shareReplay, tap } from 'rxjs/operators';
import {
  HelpExtHttpHeaders,
  HelpExtUrlResolver,
  HELP_EXT_CACHE_LIFETIME_TOKEN,
  HELP_EXT_HTTP_HEADERS,
  HELP_EXT_URL_TOKEN,
} from '../help-ext.config';
import { Article } from './article';

interface ICacheEntry {
  article?: Article;
  error?: any;
}

export const cacheLifetimeSecondDefault = 30 * 60;

@Injectable()
export class HelpExtService {
  private readonly cacheLifetime: number;

  private readonly cacheById: { [id: number]: ICacheEntry } = {};
  private readonly requestCacheById: { [id: number]: Observable<Article> } = {};
  private readonly cacheByUid: { [url: string]: ICacheEntry } = {};
  private readonly requestCacheByUid: { [url: string]: Observable<Article> } = {};
  private readonly cacheByUrl: { [url: string]: ICacheEntry } = {};
  private readonly requestCacheByUrl: { [url: string]: Observable<Article> } = {};

  readonly baseAddress: Observable<string>;

  constructor(
    helpExtUrlResolver: HelpExtUrlResolver,
    private readonly _http: HttpClient,
    private readonly _sanitizer: DomSanitizer,
    @Inject(HELP_EXT_URL_TOKEN) helpExtUrl: string,
    @Inject(HELP_EXT_CACHE_LIFETIME_TOKEN) cacheLifetimeSecond: number,
    @Inject(HELP_EXT_HTTP_HEADERS) private readonly _httpHeaders: HelpExtHttpHeaders,
  ) {
    if (helpExtUrlResolver) {
      const resolved = helpExtUrlResolver.resolve();
      if (typeof resolved === 'string') {
        this.baseAddress = of(resolved).pipe(shareReplay(1));
      } else if (isObservable(resolved)) {
        this.baseAddress = resolved.pipe(shareReplay(1));
      } else {
        throw Error('Not supported helpExtUrlResolver');
      }
    } else if (helpExtUrl) {
      this.baseAddress = of(helpExtUrl).pipe(shareReplay(1));
    } else {
      throw Error('Please, configure helpExtUrl or helpExtUrlResolver');
    }

    this.cacheLifetime = cacheLifetimeSecond * 1000;
  }

  getArticleById(id: number): Observable<Article> {
    return this.baseAddress.pipe(
      mergeMap(baseAddress =>
        this.cachedGet(this.cacheById, this.requestCacheById, id, `${baseAddress}/api/Clients/GetArticleById/${id}`),
      ),
    );
  }

  getArticleByUid(siteOrigin: string, uid: string): Observable<Article> {
    return this.baseAddress.pipe(
      mergeMap(baseAddress =>
        this.cachedGet(
          this.cacheByUid,
          this.requestCacheByUid,
          uid,
          `${baseAddress}/api/Clients/GetArticleByUid?siteOrigin=${encodeURIComponent(
            siteOrigin,
          )}&uid=${encodeURIComponent(uid)}`,
        ),
      ),
    );
  }

  getArticleByUrl(url: string): Observable<Article> {
    return this.baseAddress.pipe(
      mergeMap(baseAddress =>
        this.cachedGet(
          this.cacheByUrl,
          this.requestCacheByUrl,
          url,
          `${baseAddress}/api/Clients/GetArticleByUrl?url=${encodeURIComponent(url)}`,
        ),
      ),
    );
  }

  sanitizeContent(content: string): string {
    return this._sanitizer.sanitize(SecurityContext.HTML, this._sanitizer.bypassSecurityTrustHtml(content));
  }

  makeAbsoluteLinks(element: Element | ElementRef, baseAddress: string, linkTarget?: string): void {
    const el = element instanceof ElementRef ? (element.nativeElement as Element) : element;

    const links = el.getElementsByTagName('a');
    for (let i = 0; i < links.length; i++) {
      if (linkTarget) {
        links[i].setAttribute('target', linkTarget);
      }

      let href = links[i].getAttribute('href');
      if (/(?:^[a-z][a-z0-9+.-]*:|\/\/)/.test(href)) continue;

      href = `${baseAddress}/${href}`;
      links[i].setAttribute('href', href);
    }
  }

  private cachedGet(
    cache: { [id: number]: ICacheEntry } | { [url: string]: ICacheEntry },
    requestCache: { [id: number]: Observable<Article> } | { [id: number]: Observable<Article> },
    key: number | string,
    httpUrl: string,
  ): Observable<Article> {
    if (cache[key]) {
      return cache[key].error ? throwError(cache[key].error) : of(cache[key].article);
    }

    if (requestCache[key]) return requestCache[key];

    const request: { headers?: HelpExtHttpHeaders } = {};
    if (this._httpHeaders) {
      request.headers = this._httpHeaders;
    }
    requestCache[key] = this._http.get(httpUrl, request).pipe(
      map(a => a as Article),
      tap(a => {
        delete requestCache[key];
        cache[key] = {
          article: a,
        };
      }),
      catchError(e => {
        delete requestCache[key];
        cache[key] = {
          error: e,
        };
        return throwError(e);
      }),
      finalize(() => {
        setTimeout(() => delete cache[key], this.cacheLifetime);
      }),
      share(),
    );

    return requestCache[key];
  }
}
