import { Overlay, OverlayContainer, OverlayRef, ViewportRuler } from '@angular/cdk/overlay';
import { Platform } from '@angular/cdk/platform';
import { ComponentPortal } from '@angular/cdk/portal';
import { DOCUMENT } from '@angular/common';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  HostListener,
  Inject,
  Input,
  OnDestroy,
  OnInit,
  ViewChild,
} from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { Subject, Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { Article } from '../api/article';
import { HelpExtService } from '../api/help-ext.service';
import { HelpExtFlyoutComponent } from '../help-ext-flyout/help-ext-flyout.component';
import { FlexibleConnectedPositionStrategy17 } from './flexible-connected-position-strategy-v17';

const FLYOUT_SHOW_HIDE_DELAY = 100;

@Component({
  selector: 'help-ext',
  templateUrl: './help-ext.template.html',
  host: {
    class: 'help-ext',
  },
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HelpExtComponent implements OnDestroy, OnInit {
  private _article: Article;
  public get article(): Article {
    return this._article;
  }
  public set article(value: Article) {
    this._article = value;
    this._cdr.markForCheck();
  }

  private _routerEventsSubscription: Subscription;
  private _articleId: number;
  private _articleUid: string;
  private _byLocation: boolean;

  @Input()
  get articleId(): number {
    return this._articleId;
  }
  set articleId(value: number) {
    if (this.articleUid) throw Error('Do not use articleId with articleUid');
    if (this.byLocation) throw Error('Do not use articleId with byLocation');

    if (this._articleId === value) return;

    this._articleId = value;

    if (!value) return;
    this._api.getArticleById(value).subscribe(
      article => (this.article = article),
      () => (this.article = null),
    );
  }

  @Input()
  get articleUid(): string {
    return this._articleUid;
  }
  set articleUid(value: string) {
    if (this.articleId) throw Error('Do not use articleUid with articleId');
    if (this.byLocation) throw Error('Do not use articleUid with byLocation');

    if (this._articleUid === value) return;

    this._articleUid = value;

    if (!value) return;
    this._api.getArticleByUid(location.origin, value).subscribe(
      article => (this.article = article),
      () => (this.article = null),
    );
  }

  @Input()
  get byLocation(): boolean {
    return this._byLocation;
  }
  set byLocation(value: boolean) {
    if (this.articleId) throw Error('Do not use byLocation with articleId');
    if (this.articleUid) throw Error('Do not use byLocation with articleUid');

    if (this._byLocation === value) return;

    this._byLocation = value;

    if (value) {
      this.updateFromLocation();
      this._routerEventsSubscription = this._router.events
        .pipe(filter(event => event instanceof NavigationEnd))
        .subscribe(() => {
          this.updateFromLocation();
        });
    } else {
      this._routerEventsSubscription?.unsubscribe();
    }
  }

  @Input()
  alwaysVisible: boolean = true;

  @Input()
  inline: boolean = false;

  @ViewChild('helpQuestion', { static: true })
  private _helpQuestionElement: ElementRef;

  private _overlayRef: OverlayRef;
  private _portal: ComponentPortal<HelpExtFlyoutComponent>;
  private _flyoutInstance: HelpExtFlyoutComponent;

  private _onDestroy = new Subject<void>();

  private _showTimeoutId: ReturnType<typeof setTimeout> | undefined;
  private _hideTimeoutId: ReturnType<typeof setTimeout> | undefined;

  @HostListener('mouseenter')
  _onMouseEnter() {
    if (!this.article) return;

    this.show(FLYOUT_SHOW_HIDE_DELAY);
  }

  @HostListener('mouseleave')
  _onMouseLeave() {
    this.hide(FLYOUT_SHOW_HIDE_DELAY);
  }

  private show(delay: number): void {
    clearTimeout(this._hideTimeoutId);
    clearTimeout(this._showTimeoutId);
    if (this._flyoutInstance) return;

    this._showTimeoutId = setTimeout(() => {
      this._flyoutInstance = this._overlayRef.attach(this._portal).instance;
      this._flyoutInstance.article = this.article;
      this._flyoutInstance.mouseEnter.subscribe(() => this.show(FLYOUT_SHOW_HIDE_DELAY));
      this._flyoutInstance.mouseLeave.subscribe(() => this.hide(FLYOUT_SHOW_HIDE_DELAY));
      this._flyoutInstance.show();

      setTimeout(() => this._overlayRef.updatePosition());

      this._showTimeoutId = undefined;
    }, delay);
  }

  private hide(delay: number): void {
    clearTimeout(this._hideTimeoutId);
    clearTimeout(this._showTimeoutId);
    if (!this._flyoutInstance) return;

    this._hideTimeoutId = setTimeout(() => {
      this._hideTimeoutId = undefined;

      this._flyoutInstance.hide();
      this._flyoutInstance.afterHidden.subscribe(() => {
        this._overlayRef.detach();
        this._flyoutInstance = undefined;
      });
    }, delay);
  }

  constructor(
    private readonly _cdr: ChangeDetectorRef,
    private readonly _router: Router,
    private readonly _overlay: Overlay,
    private readonly _api: HelpExtService,
    private readonly _viewportRuler: ViewportRuler,
    @Inject(DOCUMENT) private readonly _document: any,
    private readonly _platform: Platform,
    private readonly _overlayContainer: OverlayContainer,
  ) {}

  ngOnInit(): void {
    this._portal = new ComponentPortal(HelpExtFlyoutComponent);

    const positionStrategy = new FlexibleConnectedPositionStrategy17(
      this._helpQuestionElement,
      this._viewportRuler,
      this._document,
      this._platform,
      this._overlayContainer,
    )
      .withFlexibleDimensions(false)
      .withGrowAfterOpen(true)
      .withPush(true)
      .withPositions([
        { originX: 'center', originY: 'bottom', overlayX: 'center', overlayY: 'top' }, // bottom
        { originX: 'end', originY: 'center', overlayX: 'start', overlayY: 'center' }, // right
        { originX: 'start', originY: 'center', overlayX: 'end', overlayY: 'center' }, // left
        { originX: 'end', originY: 'center', overlayX: 'start', overlayY: 'top' }, // force right
        { originX: 'start', originY: 'center', overlayX: 'end', overlayY: 'top' }, // force left
        { originX: 'center', originY: 'top', overlayX: 'center', overlayY: 'bottom' }, // top
      ]);
    const scrollStrategy = this._overlay.scrollStrategies.reposition();
    this._overlayRef = this._overlay.create({
      positionStrategy: positionStrategy,
      scrollStrategy: scrollStrategy,
      //maxHeight: window.innerHeight - this._helpQuestionElement.nativeElement.getBoundingClientRect().bottom,
      maxWidth: 'min(920px, 100vw)',
      panelClass: 'help-ext-panel',
    });
  }

  ngOnDestroy(): void {
    this._onDestroy.next();
    this._onDestroy.complete();
    this._routerEventsSubscription?.unsubscribe();
  }

  private updateFromLocation(): void {
    this._api.getArticleByUrl(location.origin + location.pathname).subscribe(
      article => (this.article = article),
      () => (this.article = null),
    );
  }
}
