import { Directive, Input } from '@angular/core';
import { HelpExtService } from '../api/help-ext.service';
import { HelpExtArticleContentComponent } from '../help-ext-article-content/help-ext-article-content.component';

@Directive({
  selector: 'help-ext-article-content[uid]',
  exportAs: 'helpExtUid',
})
export class HelpExtUidDirective {
  @Input()
  set uid(value: string) {
    this._api.getArticleByUid(location.origin, value).subscribe(
      article => (this._host.article = article),
      () => (this._host.article = null),
    );
  }

  constructor(private readonly _api: HelpExtService, private readonly _host: HelpExtArticleContentComponent) {}
}
